@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;

  font-family: 'Poppins','Montserrat',sans-serif;
  color: #8B8680;

  /* border:1px white solid; */
}

body {
  background-color: #1C1B1A;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
#header-number {
    text-align: center;
    color: #282826;
    font-size: 200px;
    line-height: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

#header-month {
    color:white;
    font-size: 25pt;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: transparent;
}

#header-panel {
    z-index: -1;
    height: 200px;
    position: fixed; 
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    width: 100px;
    text-align: center;
}

#content-panel {
    height:200px;
}
#navbar-panel {
    z-index: 100;
    position: fixed; 
    left: 0;
    right: 0;
    bottom:0;
    margin-left: auto; 
    margin-right: auto; 
    max-width: 500px;
    text-align: center;

    border-top-left-radius: 28px;
    border-top-right-radius: 28px;
    background-color: #282826;
    color:#8B8680;

    box-shadow: 0px -5px 8px 0px rgb(28 27 26);
}

#navbar-panel li a:hover {
  color: white;
  transition: 0.3s;
}

#navbar-panel ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#navbar-panel li {
  float: left;
  width: calc(100% / 3);
}

#navbar-panel li a {
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.void {
    height:50px;
    background: transparent;
}
.week-container{
    padding:10px;
}

.day-panel {
    background: #282826;
    width: 95%;
    word-wrap: break-word;
    min-height: 200px;
    max-width: 500px;

    display: table;
    margin: 10px auto;

    font-size: 18px;
    border-radius: 28px;

    padding-bottom: 20px;
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 10px;
}

.small {
    min-height: 10px;
    padding-top: 20px;
    text-align: center;
    vertical-align: middle;
}

.red {
    background-color: #ff2700;
    color: white;
}

.day-panel td.day-subject-detail{
    padding-top:0px;
    width: 100%;
    max-width: 200px;
    text-align: right;
    margin-top: 10px;
}

.day-panel td.day-subject-name{
    margin-top: 10px;
    width: 100%;
}

.day-panel h2{
    font-size: 23px;
    font-weight: 200;
    text-align: center;
    color:white;
}

.highlight {
    color:white;
    text-decoration: none;
}
.active {
    color: white;
    -webkit-text-decoration: bold;
            text-decoration: bold;
}
.form {
    width:100%;
    padding-top: 10px;
}

.bottom-padding {
    padding-bottom: 10px;
}

.form > div {
    width:100%;
    margin: 0 auto;
    margin-top: 5px;
    margin-bottom: 10px;
}

.input {
    font-size:18px;
    width:100%;
    box-sizing: border-box;
    border: 0px;
    background-color: transparent;
    border-bottom: 2px solid #626262;
    transition: 0.3s;
}

input:focus, textarea:focus, select:focus{
    outline: none;
    transition: 0.3s;
}

input:hover {
    border-bottom: 2px solid white;
    color:white;
    transition: 0.3s;
}

.button {
    font-size: 17px;
    width:100%;
    margin: 0 auto;
    margin-top: 20px;
    height:45px;
    border: none;
    background-color: #373737;
    border-radius: 28px;
    transition: 0.3s;
}

.button:hover {
    color: white;
    transition: 0.3s;
    cursor: pointer;
}
